main.l-careers {
	background-color: #91E0FF;
}

.l-wrapper {
    max-width: 1440px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
	padding: 0 140px;
	@media screen and (max-width: 1240px) {
		padding: 0 100px;
	}
	@media screen and (max-width: 1024px) {
		padding: 0 70px;
	}
	@media screen and (max-width: 910px) {
		padding: 0 40px;
	}
	@media screen and (max-width: 600px) {
		padding: 0 24px;
	}
}

.l-pt-84 {
	padding-top: 84px;
	@media screen and (max-width: 768px) {
		padding-top: 40px;
	}
}
.l-pb-84 {
	padding-bottom: 84px;
	@media screen and (max-width: 768px) {
		padding-bottom: 40px;
	}
}